:root {
  /* color */
  --color_main: #997dff;
}

body {
  padding-top: 30px;
}

* {
  color: #222;
  margin: 0;
  padding: 0;
}

ul,
ol,
li {
  list-style: none;
}

button,
input {
  border: 0;
  background: none;
}

.icon {
  cursor: pointer;
}

h1 {
  color: #fff;
}

h2 {
  text-align: center;
  font-size: 24px;
}

.wrapper {
  min-width: 400px;
  max-width: 600px;
  width: 60%;
  margin: auto;
  padding: 50px 25px 20px;
  border-radius: 8px;
  box-sizing: border-box;
  background: #fff;
  border-radius: 15px;
  box-shadow: 3px 8px 20px rgba(0, 0, 0, 0.3);
}

.header {
  color: #fff;
}

.header h2 {
  margin-bottom: 20px;
}

.header b {
  display: block;
  font-size: 14px;
  text-align: right;
}

.todo_add {
  display: flex;
  gap: 10px;
  align-items: center;
  margin: 15px 0;
  padding: 14px 18px;
  border-radius: 3px;
  border: 2px solid var(--color_main);
  background: #fff;
}

.todo_add input {
  flex-grow: 1;
  font-size: 14px;
}

.todo_add button svg {
  height: 16px;
}

.todo_list li {
  display: flex;
  gap: 10px;
  align-items: center;
  margin: 10px 0;
  padding: 14px 18px;
  border-radius: 3px;
  background: var(--color_main);
  font-weight: bold;
}

.todo_list li input {
  flex-grow: 1;
  min-height: 22px;
  background: #fff;
}

.todo_list li span {
  flex-grow: 1;
  min-height: 22px;
  color: #fff;
  cursor: pointer;
  user-select: none;
}

.todo_list li .completed {
  color: #666;
  font-style: italic;
  text-decoration: line-through;
}
